import GoodFlex from '../../components/goodflex/goodflex.vue'
import LoadMore from '../../components/uni-load-more/uni-load-more.vue'
import { requestip } from '../api/api.js'
export default{
	components:{
		GoodFlex,
		LoadMore
	},
	data(){
		return{
			value:'',
			imgsrc:'../../static/yan.png',
			imgsrc2:'../../static/biyan.png',
			data:'',
			imgblean:false,
			OldKeys:[],
			historyOld:[],
			flexblean:true,
			pages:1,
			goodstate:true,
			cleanstate:true,
			showloadMore:false,
			loadMoreText:"加载中...",
			hotdata:['苹果','梨子','西瓜','海苔'],
			hotstate:true,
			scorrllTop:false,
			defaultKeyword:'服装',
			Null:false,
			vlean:false
		}
	},
	methods:{
		// 请求数据
		searchvalue(){
			// 显示商品
			var that=this;
			this.goodstate=false;
			// 隐藏历史记录
			this.cleanstate=false;
			this.loadMoreText='加载中...';
			var value=this.value?this.value:this.defaultKeyword;
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					console.log(res.data);
					var OldKeys = JSON.parse(res.data);
					var findIndex = OldKeys.indexOf(value);
					if (findIndex == -1) {
						OldKeys.unshift(value);
					} else {
						OldKeys.splice(findIndex, 1);
						OldKeys.unshift(value);
					}
					//最多10个纪录
					OldKeys.length > 10 && OldKeys.pop();
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.historyOld = OldKeys; //更新历史搜索
				},
				fail: (e) => {
					var OldKeys = [value];
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.historyOld = OldKeys; //更新历史搜索
				}
			});
			// 去重
			// 加载中...
			uni.showLoading({
				title:"加载中..."
			})
			console.log("OldKeys:",this.OldKeys);
			var Oldkey=this.OldKeys;
			this.data=[];
			uni.request({
				url: requestip()+'/thirdreturn/index/searchgoods?key_word='+value+'&page=1',
				method: 'GET',
				data: {},
				success: res => {
					console.log("搜索的数据：",res.data);
					this.data=res.data.goods_search_response.goods_list;
					// 隐藏showloading
					if(res.data.goods_search_response.goods_list==''){
						this.Null=true;
					}
					uni.hideLoading();
				},
				fail: () => {},
				complete: () => {}
			});
		},
		imgurl(){
			this.hotstate=!this.hotstate;
		},
		cleanSearch(){
			var that=this;
			uni.showModal({
				title:"提示",
				content:"你确定清除所以历史记录吗",
				success:res=> {
					if(res.confirm){
					uni.removeStorageSync('OldKeys');
					// 隐藏历史记录
					that.historyOld=[];
					that.cleanstate=false;
					}
				}
			})
			
		},
		// 点击块
		textH(i){
			// 显示商品
			this.goodstate=false;
			// 隐藏历史记录
			this.cleanstate=false;
			this.value=i;
			this.vlean=true;
			var value=i;
			// top==0
			// 去重
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					console.log(res.data);
					var OldKeys = JSON.parse(res.data);
					var findIndex = OldKeys.indexOf(value);
					if (findIndex == -1) {
						OldKeys.unshift(value);
					} else {
						OldKeys.splice(findIndex, 1);
						OldKeys.unshift(value);
					}
					//最多10个纪录
					OldKeys.length > 10 && OldKeys.pop();
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.historyOld = OldKeys; //更新历史搜索
				},
				fail: (e) => {
					var OldKeys = [value];
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.historyOld = OldKeys; //更新历史搜索
				}
			});
			// 去重
			uni.request({
				url: requestip()+'/thirdreturn/index/searchgoods?key_word='+this.value+'&page=1',
				method: 'GET',
				data: {},
				success: res => {
					console.log("搜索的数据：",res.data);
					this.data=res.data.goods_search_response.goods_list;
					// 隐藏showloading
					uni.hideLoading();
						if(res.data.goods_search_response.goods_list==''){
						this.Null=true;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		valueDt(){
			this.vlean=true;
		},
		clearDall(){
			this.value='';
			this.vlean=false;
		}
	},
	onLoad:function(e) {
		var oldkey=uni.getStorageSync('OldKeys');
		//继承上一次的搜索记录
		this.OldKeys=uni.getStorageSync("OldKeys");
		if(oldkey==''){
			this.cleanstate=false;
		}else{
			this.historyOld=JSON.parse(oldkey);
		}
		this.value=e.name;
		console.log('ssss',e.name);
		if(e.name!=undefined){
			this.searchvalue();
			this.vlean=true;
		}
	},
	// 无限加载
	onReachBottom() {
		this.pages+=1;
		this.showloadMore=true;
		uni.request({
			url: requestip()+'/thirdreturn/index/searchgoods?key_word='+this.value+'&page='+this.pages,
			method: 'GET',
			data: {},
			success: res => {
				console.log("wuxian:",res.data);
				for(var i=0;i<res.data.goods_search_response.goods_list.length;i++){
					this.data.push(res.data.goods_search_response.goods_list[i])
				}
				this.showloadMore=false;
				if(res.data.goods_search_response.goods_list==0){
					this.loadMoreText='没有更多数据了';
					this.showloadMore=true;
				}else{
					this.loadMoreText='加载中...';
					this.showloadMore=false;
				}
			},
			fail: () => {},
			complete: () => {}
		});
	},
	// 定位
	onPageScroll(e) {
		// console.log("gun:",e.scrollTop);
		if(e.scrollTop>=40){
			this.scorrllTop=true;
		}else{
			this.scorrllTop=false;
		}
	}
}
